@import "./../../common/styles/sass/variables";
@import "./src/common/styles/sass/mixins.scss";

.project {
  width: 485px;
  height: 290px;
  background-size: cover;
  background-position: center;
  margin: 0 0 28px;
  position: relative;
  .content {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include text(Raleway-ExtraBold, sans-serif, 16px);
    text-transform: uppercase;
    color: $textSeconsdColor;
    opacity: 0;
    transition: all 0.3s steps(20);
    .icon{
      width: 20px;
      height: 20px;
      color: $textSeconsdColor;
      margin-top: 60px;
      padding: 10px;
      border-radius: 5px;
      background-color: white;
      box-shadow: 0 0 7px $textSeconsdColor;
      transition: all 0.3s steps(20);
    }
  }
  &::before {
    position: absolute;
    content: "";
    background-color: white;
    top: 25px;
    bottom: 25px;
    left: 25px;
    right: 25px;
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }
  &:hover::before {
    position: absolute;
    content: "";
    background-color: white;
    top: 10px;
    bottom: 10px;
    left: 10px;
    right: 10px;
    opacity: 0.9;
    transition: all 0.3s ease-in-out;
  }
  &:hover .content{
    opacity: 1;
    transition: all 0.5s steps(20);
    .icon{
      margin-top: 40px;
      transition: all 0.3s steps(20);
    }
  }
}
@media screen and (max-width: 1212px){
  .project {
    width: 450px;
    height: 250px;
    margin: 0 0 10px;
  }
}
@media screen and (max-width: 1128px){
  .project {
    width: 350px;
    height: 200px;
  }
}
@media screen and (max-width: 880px){
  .project {
    width: 320px;
    height: 170px;
  }
}
@media screen and (max-width: 400px){
  .project {
    width: 100%;
  }
}